/* barriecito-regular - latin */
@font-face {
  font-family: 'Barriecito';
  font-style: normal;
  font-weight: 400;
  src: url('./assets/fonts/barriecito-v2-latin-regular.eot'); /* IE9 Compat Modes */
  src: local('Barriecito Regular'), local('Barriecito-Regular'),
       url('./assets/fonts/barriecito-v2-latin-regular.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
       url('./assets/fonts/barriecito-v2-latin-regular.woff2') format('woff2'), /* Super Modern Browsers */
       url('./assets/fonts/barriecito-v2-latin-regular.woff') format('woff'), /* Modern Browsers */
       url('./assets/fonts/barriecito-v2-latin-regular.ttf') format('truetype'), /* Safari, Android, iOS */
       url('./assets/fonts/barriecito-v2-latin-regular.svg#Barriecito') format('svg'); /* Legacy iOS */
}
